/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input, select {
  vertical-align: middle;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.4;
  font-family: "Moderat", sans-serif;
  font-size: 0.875rem;
  color: #262c55;
  font-weight: 300;
}

a, a:visited, a:hover {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

.btn:not(:first-child) {
  padding: 0.875rem 2.8rem;
  border-radius: 10px;
  cursor: pointer;
  border: none;
  background-color: #0a0e27;
  color: #fff;
}

@media (max-width: 56.3125em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 56.375em) {
  .hide-for-desktop {
    display: none;
  }
}

header nav {
  padding: 14px 20px 20px 6px;
}
header .HH {
  display: block;
  width: 12rem;
}
header .header__menu span {
  display: block;
  width: 26px;
  height: 2px;
  background-color: #000;
  transition: all 300ms ease-in-out;
  transform-origin: 3px 1px;
}
header .header__menu span:not(:last-child) {
  margin-bottom: 5px;
}
header .header__menu.open > span:first-child {
  transform: rotate(45deg);
}
header .header__menu.open > span:nth-child(2) {
  opacity: 0;
}
header .header__menu.open > span:last-child {
  transform: rotate(-45deg);
}
header .header__links {
  position: relative;
}
header .header__links a {
  color: #262c55;
  font-size: 0.875rem;
  font-weight: 550;
}
header .header__links a:not(:nth-child(1), :nth-child(2)):hover {
  color: #00abab;
}
header .header__links .header__dropdown {
  padding: 2rem;
}
header .header__links .header__dropdown__payments {
  padding: 2rem;
}
header .header__links .header__dropdown__payments .menu {
  display: none;
  position: absolute;
  top: 4rem;
  border-radius: 0.625rem;
  left: -4rem;
  width: 22rem;
  background-color: #ffffff;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
}
header .header__links .header__dropdown__payments .menu .__cur {
  padding: 1rem;
}
header .header__links .header__dropdown__payments .menu .__cur .text {
  padding-left: 20px;
}
header .header__dropdown__payments:hover .menu {
  display: block;
  transform: translateY(0);
}
header .header__dropdown__trade {
  padding: 2rem;
}
header .header__dropdown__trade .menu {
  display: none;
  position: absolute;
  top: 4rem;
  border-radius: 0.625rem;
  left: 6.5rem;
  width: 22rem;
  background-color: #ffffff;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
}
header .header__dropdown__trade .menu .__cur {
  padding: 1rem;
  transition: all 300ms ease-in-out;
}
header .header__dropdown__trade .menu .__cur .text {
  padding-left: 20px;
}
header .header__dropdown__trade:hover .menu {
  display: block;
}
header .header__btn {
  padding: 0.875rem 2.8rem;
  border-radius: 10px;
  cursor: pointer;
  border: none;
}
header .header__btn:hover {
  background-color: #0a0e27;
  color: #fff;
}

.sidebar {
  position: fixed;
  top: 34px;
  bottom: 0;
  height: calc(100vh - 34px);
  background-color: #fff;
  padding: 1.25rem;
}
.sidebar h3 {
  margin-bottom: 1.25rem;
}
.sidebar h3:not(:first-child) {
  margin-top: 1.25rem;
}